import { Injectable } from '@angular/core';
import { Data } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class ValidationService {

  constructor() { }

  assureNotEmpty(str: string): boolean {
    if (str) {
      if (str.trim().length > 0) {
        return true;
      }
      else return false;
    }
    else return false;
  }

  assureAreEqual(str1: string, str2: string): boolean {
    if (str1 && str2) {
      if (str1.trim() == str2.trim()) {
        return true;
      }
      else return false;
    }
    else return false;
  }

  assureRegLogin(str: string): boolean {
    let reg = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;

    if (reg.test(str) == true) {
      return true;
    }
    else return false;
  }

  assureRegPasswords(str: string): boolean {
    let reg = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[^\w\s]).{6,}/;

    if (reg.test(str) == true) {
      return true;
    }
    else return false;
  }

  assureDateAge(date: string): number {
    let now = new Date();
    let today = new Date(now.getFullYear(), now.getMonth(), now.getDate()); //Текущя дата без времени
    let dob = new Date(date);
    let dobnow = new Date(today.getFullYear(), dob.getMonth(), dob.getDate()); //ДР в текущем году
    let age: number = today.getFullYear() - dob.getFullYear();

    //Если ДР в этом году ещё предстоит, то вычитаем из age один год
    if (today < dobnow) {
      age = age - 1;
    }

    return age;
  }

  assureTeh(teh): boolean {
    if (teh.length > 2) {
      return true;
    }
    else return false;
  }

  remember(rem_check): boolean {
    if (rem_check.checked) {
      return true;
    }
    else return false;
  }

  getCookie(name) {
    let matches = document.cookie.match(new RegExp(
        "(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
    ));
    return matches ? decodeURIComponent(matches[1]) : undefined;
}






}